"use client";

import React, { useState, useEffect } from "react";
import { X, CheckCircle, AlertTriangle } from "lucide-react";
import { apiRequest } from "@/app/authservice/api";
import { toast } from "react-hot-toast";

const PatientUpdateModal = ({ onClose, patient = null, onUpdated }) => {
  const [loading, setLoading] = useState(false);
  const [formData, setFormData] = useState({
    patientId: "",
    fullName: "",
    phone: "",
    emergencyPhone: "",
    email: "",
    address: "",
    age: "",
    gender: "",
    bloodGroup: "",
  });
  const [formErrors, setFormErrors] = useState({});

  /* Initialize form */
  useEffect(() => {
    if (patient) {
      setFormData({
        patientId: patient.patientId || "",
        fullName: patient.fullName || "",
        phone: patient.phone || "",
        emergencyPhone: patient.emergencyPhone || "",
        email: patient.email || "",
        address: patient.address || "",
        age: patient.age || "",
        gender: patient.gender || "",
        bloodGroup: patient.bloodGroup || "",
      });
    } else {
      setFormData({
        patientId: `PAT-${Date.now()}`,
        fullName: "",
        phone: "",
        emergencyPhone: "",
        email: "",
        address: "",
        age: "",
        gender: "",
        bloodGroup: "",
      });
    }
  }, [patient]);

  /* Handle input changes */
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
    setFormErrors((prev) => ({ ...prev, [name]: "" }));
  };

  /* Handle form submit */
  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validation
    const errors = {};
    if (!formData.fullName) errors.fullName = "Name is required.";
    if (!formData.phone) errors.phone = "Phone is required.";
    if (!formData.address) errors.address = "Address is required.";
    if (!formData.age) errors.age = "Age is required.";
    if (!formData.gender) errors.gender = "Gender is required.";

    if (Object.keys(errors).length > 0) {
      setFormErrors(errors);
      return;
    }

    const payload = {
      patientId: formData.patientId,
      fullName: formData.fullName,
      phone: formData.phone,
      emergencyPhone: formData.emergencyPhone || "",
      email: formData.email || "",
      address: formData.address,
      age: Number(formData.age),
      gender: formData.gender,
      bloodGroup: formData.bloodGroup || "",
    };

    try {
      setLoading(true);
      if (patient) {
        // Update existing patient
        const res = await apiRequest(
          `/patients/updatePatient/${patient._id}`,
          "PUT",
          payload
        );

        if (res?.success) {
          toast.success("Patient updated successfully");
          onUpdated && onUpdated(); // callback to refresh list
          onClose();
        } else {
          toast.error(res?.message || "Failed to update patient");
        }
      } else {
        // Create new patient
        const res = await apiRequest("/patients/createPatient", "POST", payload);
        if (res?.success) {
          toast.success("Patient created successfully");
          onUpdated && onUpdated();
          onClose();
        } else {
          toast.error(res?.message || "Failed to create patient");
        }
      }
    } catch (error) {
      console.error("Save patient error:", error);
      toast.error(error?.response?.data?.message || "Something went wrong");
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-2xl shadow-xl w-full max-w-3xl max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="sticky top-0 bg-teal-600 text-white p-6 flex justify-between items-center rounded-t-2xl">
          <h2 className="text-2xl font-bold">
            {patient ? "Edit Patient" : "New Patient"}
          </h2>
          <button onClick={onClose} className="p-2 hover:bg-white/20 rounded-lg">
            <X className="w-6 h-6" />
          </button>
        </div>

        {/* Form */}
        <form className="p-6 space-y-6" onSubmit={handleSubmit}>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium">Patient ID</label>
              <input
                type="text"
                value={formData.patientId}
                readOnly
                className="w-full h-9 rounded-md border bg-gray-100 px-3"
              />
            </div>
            <div>
              <label className="text-sm font-medium">Full Name *</label>
              <input
                type="text"
                name="fullName"
                value={formData.fullName}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              />
              {formErrors.fullName && (
                <p className="text-red-600 text-xs mt-1">{formErrors.fullName}</p>
              )}
            </div>
            <div>
              <label className="text-sm font-medium">Phone *</label>
              <input
                type="tel"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              />
              {formErrors.phone && (
                <p className="text-red-600 text-xs mt-1">{formErrors.phone}</p>
              )}
            </div>
            <div>
              <label className="text-sm font-medium">Emergency Phone</label>
              <input
                type="tel"
                name="emergencyPhone"
                value={formData.emergencyPhone}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              />
            </div>
            <div>
              <label className="text-sm font-medium">Email</label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              />
            </div>
            <div>
              <label className="text-sm font-medium">Address *</label>
              <textarea
                name="address"
                rows={2}
                value={formData.address}
                onChange={handleChange}
                className="w-full rounded-md border px-3 py-2"
              />
              {formErrors.address && (
                <p className="text-red-600 text-xs mt-1">{formErrors.address}</p>
              )}
            </div>
            <div>
              <label className="text-sm font-medium">Age *</label>
              <input
                type="number"
                name="age"
                value={formData.age}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              />
              {formErrors.age && (
                <p className="text-red-600 text-xs mt-1">{formErrors.age}</p>
              )}
            </div>
            <div>
              <label className="text-sm font-medium">Gender *</label>
              <select
                name="gender"
                value={formData.gender}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              >
                <option value="">Select</option>
                <option value="Male">Male</option>
                <option value="Female">Female</option>
                <option value="Other">Other</option>
              </select>
              {formErrors.gender && (
                <p className="text-red-600 text-xs mt-1">{formErrors.gender}</p>
              )}
            </div>
            <div>
              <label className="text-sm font-medium">Blood Group</label>
              <input
                type="text"
                name="bloodGroup"
                value={formData.bloodGroup}
                onChange={handleChange}
                className="w-full h-9 rounded-md border px-3"
              />
            </div>
          </div>

          <div className="flex justify-end gap-3 pt-4">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 border rounded-md"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={loading}
              className={`px-4 py-2 rounded-md text-white ${
                loading ? "bg-gray-400" : "bg-teal-600 hover:bg-teal-700"
              }`}
            >
              {loading ? (patient ? "Updating..." : "Creating...") : patient ? "Update Patient" : "Create Patient"}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default PatientUpdateModal;
